html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  
}

body {
  
  margin: 0;
}

main {
  display: block
}

h1 {
  font-size: 2em;
  margin: .67em 0
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible
}

pre {
  font-family: monospace, monospace;
  font-size: 1em
}

a {
  background-color: transparent
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted
}

b, strong {
  font-weight: bolder
}

code, kbd, samp {
  font-family: monospace, monospace;
  font-size: 1em
}

small {
  font-size: 80%
}

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline
}

sub {
  bottom: -.25em
}

sup {
  top: -.5em
}

img {
  border-style: none
}

button, input, optgroup, select, textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0
}

button, input {
  overflow: visible
}

button, select {
  text-transform: none
}

button, [type=button], [type=reset], [type=submit] {
  -webkit-appearance: button
}

button::-moz-focus-inner, [type=button]::-moz-focus-inner, [type=reset]::-moz-focus-inner, [type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0
}

button:-moz-focusring, [type=button]:-moz-focusring, [type=reset]:-moz-focusring, [type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText
}

fieldset {
  padding: .35em .75em .625em
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal
}

progress {
  vertical-align: baseline
}

textarea {
  overflow: auto
}

[type=checkbox], [type=radio] {
  box-sizing: border-box;
  padding: 0
}

[type=number]::-webkit-inner-spin-button, [type=number]::-webkit-outer-spin-button {
  height: auto
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit
}

details {
  display: block
}

summary {
  display: list-item
}

template {
  display: none
}

[hidden] {
  display: none
}

code[class*=language-], pre[class*=language-] {
  color: #ccc;
  background: 0 0;
  font-family: Consolas, Monaco, andale mono, ubuntu mono, monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none
}

pre[class*=language-] {
  padding: 1em;
  margin: .5em 0;
  overflow: auto
}

:not(pre)>code[class*=language-], pre[class*=language-] {
  background: #2d2d2d
}

:not(pre)>code[class*=language-] {
  padding: .1em;
  border-radius: .3em;
  white-space: normal
}

.token.comment, .token.block-comment, .token.prolog, .token.doctype, .token.cdata {
  color: #999
}

.token.punctuation {
  color: #ccc
}

.token.tag, .token.attr-name, .token.namespace, .token.deleted {
  color: #e2777a
}

.token.function-name {
  color: #6196cc
}

.token.boolean, .token.number, .token.function {
  color: #f08d49
}

.token.property, .token.class-name, .token.constant, .token.symbol {
  color: #f8c555
}

.token.selector, .token.important, .token.atrule, .token.keyword, .token.builtin {
  color: #cc99cd
}

.token.string, .token.char, .token.attr-value, .token.regex, .token.variable {
  color: #7ec699
}

.token.operator, .token.entity, .token.url {
  color: #67cdcc
}

.token.important, .token.bold {
  font-weight: 700
}

.token.italic {
  font-style: italic
}

.token.entity {
  cursor: help
}

.token.inserted {
  color: green
}

:root {
  --phoneWidth: (max-width: 684px);
  --tabletWidth: (max-width: 900px)
}




.button-container {
  display: table;
  margin-left: auto;
  margin-right: auto
}

button, .button, a.button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 18px;
  margin-bottom: 5px;
  background: #eaeaea;
  text-decoration: none;
  text-align: center;
  font-weight: 500;
  border-radius: 8px;
  border: 1px solid transparent;
  appearance: none;
  cursor: pointer;
  outline: none
}

.dark-theme button, .dark-theme .button, .dark-theme a.button {
  background: #23303d;
  color: inherit
}

button.outline, .button.outline, a.button.outline {
  background: 0 0;
  border-color: #eaeaea;
  box-shadow: none;
  padding: 8px 18px
}

.dark-theme button.outline, .dark-theme .button.outline, .dark-theme a.button.outline {
  border-color: #23303d;
  color: inherit
}

button.outline :hover, .button.outline :hover, a.button.outline :hover {
  transform: none;
  box-shadow: none
}

button.primary, .button.primary, a.button.primary {
  box-shadow: 0 4px 6px rgba(50, 50, 93, .11), 0 1px 3px rgba(0, 0, 0, .08)
}

button.primary:hover, .button.primary:hover, a.button.primary:hover {
  box-shadow: 0 2px 6px rgba(50, 50, 93, .21), 0 1px 3px rgba(0, 0, 0, .08)
}

button.link, .button.link, a.button.link {
  background: 0 0;
  font-size: 1rem
}

button.small, .button.small, a.button.small {
  font-size: .8rem
}

button.wide, .button.wide, a.button.wide {
  min-width: 200px;
  padding: 14px 24px
}

.code-toolbar {
  margin-bottom: 20px
}

.code-toolbar .toolbar-item a {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 3px 8px;
  margin-bottom: 5px;
  background: #eaeaea;
  text-decoration: none;
  text-align: center;
  font-size: 13px;
  font-weight: 500;
  border-radius: 8px;
  border: 1px solid transparent;
  appearance: none;
  cursor: pointer;
  outline: none
}

.dark-theme .code-toolbar .toolbar-item a {
  background: #23303d;
  color: inherit
}

.header {
  background: #fafafa;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 20px
}

.dark-theme .header {
  background: #23303d
}

.header__right {
  display: flex;
  flex-direction: row;
  align-items: center
}

@media(max-width:684px) {
  .header__right {
      flex-direction: row-reverse
  }
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  width: 760px;
  max-width: 100%
}

.theme-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  cursor: pointer
}

.theme-toggler {
  fill: currentColor
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  font-weight: 700
}

.logo img {
  height: 44px
}

.logo__mark {
  margin-right: 5px
}

.logo__text {
  font-size: 1.125rem
}

.logo__cursor {
  display: inline-block;
  width: 10px;
  height: 1rem;
  background: #f39c12;
  margin-left: 5px;
  border-radius: 1px;
  animation: cursor 1s infinite
}

@media(prefers-reduced-motion:reduce) {
  .logo__cursor {
      animation: none
  }
}

@keyframes cursor {
  0% {
      opacity: 0
  }
  50% {
      opacity: 1
  }
  100% {
      opacity: 0
  }
}

.menu {
  background: #fafafa;
  border-right: 1px solid;
  margin-right: 18px;
  z-index: 9999
}

.dark-theme .menu {
  background: #23303d
}

@media(max-width:684px) {
  .menu {
      position: absolute;
      top: 50px;
      right: 0;
      border: none;
      margin: 0;
      padding: 10px
  }
}

.menu__inner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  font-size: 1rem;
  list-style: none
}

.menu__inner li {
  margin: 0 12px
}

@media(max-width:684px) {
  .menu__inner {
      flex-direction: column;
      align-items: flex-start;
      padding: 0
  }
  .menu__inner li {
      margin: 0;
      padding: 5px
  }
}

.menu-trigger {
  width: 24px;
  height: 24px;
  fill: currentColor;
  margin-left: 10px;
  cursor: pointer
}

.menu a {
  display: inline-block;
  margin-right: 15px;
  text-decoration: none
}

.menu a:hover {
  text-decoration: underline
}

.menu a:last-of-type {
  margin-right: 0
}

html {
  box-sizing: border-box;
  line-height: 1.6;
  letter-spacing: .06em;
  scroll-behavior: smooth
}

*, *:before, *:after {
  box-sizing: inherit
}

